@keyframes pulse-small {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(1.7);
    opacity: 0;
  }
}
@keyframes pulse-big {
  0% {
    transform: translate(-50%, -50%) scale(0.8);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: translate(-50%, -50%) scale(2.7);
    opacity: 0;
  }
}
@keyframes pulse {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    transform: scale(3);
    opacity: 0;
  }
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  -moz-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -o-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  font-family: "Inter", sans-serif;
  background-color: #ffffff;
  color: #070c3a;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  /* iOS fix */
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  -o-font-smoothing: antialiased;
  -webkit-locale: auto;
  white-space: normal;
}
@media only screen and (max-width: 1279px) {
  body {
    font-size: calc(18px + (20 - 18) * (100vw - 992px) / (1280 - 992));
  }
}
@media only screen and (max-width: 991px) {
  body {
    font-size: calc(16px + (18 - 16) * (100vw - 768px) / (992 - 768));
  }
}
@media only screen and (max-width: 767px) {
  body {
    font-size: calc(14px + (16 - 14) * (100vw - 0px) / (768 - 0));
  }
}

.bottom-overflow {
  overflow: hidden;
}

main {
  position: relative;
  z-index: 1;
}

.main-outer {
  overflow: hidden;
}

a {
  color: #070c3a;
  text-decoration: none;
}

svg {
  width: auto;
  height: auto;
}

img {
  display: block;
  max-width: 100%;
}

input,
button {
  -webkit-appearance: none;
}

input,
button,
select,
textarea {
  font: inherit;
  background-color: transparent;
  border: 0;
  border-radius: 0;
  outline: 0;
}

input,
textarea {
  padding: 0;
  margin: 0;
}
input:focus::placeholder,
textarea:focus::placeholder {
  opacity: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

[hidden] {
  display: none !important;
}

.cursor-cell {
  cursor: cell;
}

/* Blocks */
.container {
  max-width: 1202px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}
@media only screen and (max-width: 575px) {
  .container {
    padding: 0 15px;
  }
}
.container--big {
  max-width: 1400px;
}

.table__wrapper {
  margin-top: 40px;
}
.table__sticky-menu {
  position: sticky;
  top: 0;
  left: 0;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}
.table__sticky-menu .btn {
  align-self: flex-end;
}
.table__create {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 10px;
}
.table label {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin: 0;
}
.table input {
  height: 26px;
  margin: 0;
}
.table table {
  width: 100%;
  border-collapse: collapse;
}
.table table th,
.table table td {
  border: 1px solid #ddd !important;
  padding: 10px;
  user-select: none;
}
.table table th {
  font-size: 14px;
  font-weight: 700;
}
.table table td {
  font-size: 12px;
}
.table table td.focused {
  outline: 1px solid #000;
}